/* ---->  Stripe key  <---- */
// const KEY = process.env.REACT_APP_STRIPE;
const KEY ="pk_test_51Kj8JCAfoReX6TchCLkfdn2sks73tnlyxU75WiiZxFc5dvNdAgN7X3Mu9eZRNDG2m4HOmVn8jX4D9lSN2WdfSQKq00Ny6g1Zbc";


const Cart = () => {

    const cart = useSelector(state=>state.cart);
    const [stripeToken, setToken] = useState(null);
    const navigate = useNavigate();

    const onToken = (token) => {
        setToken(token);
    }

    useEffect(()=> {
        const makeRequest = async () => {
            try{
                const res = await userRequest.post("/checkout/payment", 
                {tokenId: stripeToken.id,
                amount: cart.total * 100,
                });
                res && navigate("/success", {state : {stripeData : res.data, cart : cart}});
            }catch(err) {

            }
        }
        stripeToken && cart.total >= 1 && makeRequest();
    },[stripeToken, navigate, cart])

  return (
    <Container>
        <Navbar />
        <Announcement />
        <Wrapper>
            <Title>YOUR CART</Title>
            <Top>
                <TopButton>CONTINUE SHOPPING</TopButton>
                <TopTexts>
                    <TopText>
                        Shopping Cart (2)
                    </TopText>
                    <TopText>
                        Your Wishlist (0)
                    </TopText>
                </TopTexts>
                <TopButton type="filled">CHECKOUT NOW</TopButton>
            </Top>
            <Bottom>
                <Info>
                    {cart.products.map((product)=>(<Product key={product._id}>
                        <ProductDetail>
                            <Image src={product.img}/>
                            <Details>
                                <ProductName>
                                    <b>Product:</b> {product.title}
                                </ProductName>
                                <ProductId>
                                    <b>ID:</b> {product._id}
                                </ProductId>
                                <ProductColor color={product.color} />
                                <ProductSize>
                                    <b>Size:</b> {product.size}
                                </ProductSize>
                            </Details>
                        </ProductDetail>
                        <PriceDetail>
                            <ProductAmountContainer>
                                <Add />
                                <ProductAmount>{product.quantity}</ProductAmount>
                                <Remove />
                            </ProductAmountContainer>
                            <ProductPrice>$ {product.price * product.quantity}</ProductPrice>
                        </PriceDetail>
                    </Product>))}
                    <Hr />        
                </Info>
                <Summary>
                    <SummaryTitle>ORDER SUMMARY</SummaryTitle>
                    <SummaryItem>
                        <SummaryItemText>
                            Subtotal
                        </SummaryItemText>
                        <SummaryItemPrice>
                            $ {cart.total}
                        </SummaryItemPrice>
                    </SummaryItem>
                    <SummaryItem>
                        <SummaryItemText>
                            Estimated Shipping
                        </SummaryItemText>
                        <SummaryItemPrice>
                            $ 5.90
                        </SummaryItemPrice>
                    </SummaryItem>
                    <SummaryItem>
                        <SummaryItemText>
                            Discount
                        </SummaryItemText>
                        <SummaryItemPrice>
                            $ -5.90
                        </SummaryItemPrice>
                    </SummaryItem>
                    <SummaryItem type="total">
                        <SummaryItemText>
                            Total
                        </SummaryItemText>
                        <SummaryItemPrice>
                            $ {cart.total}
                        </SummaryItemPrice>
                    </SummaryItem>
                    <StripeCheckout
                    name="E-commerce"
                    shippingAddress
                    billingAddress
                    description={`Your total is ${cart.total}`}
                    amount={cart.total * 100}
                    token={onToken}
                    stripeKey={KEY}
                    >
                        <Button>
                            CHECKOUT NOW
                        </Button>
                    </StripeCheckout>
                </Summary>
            </Bottom>
        </Wrapper>
        <Footer />
    </Container>
  )
}

export default Cart;